type prop = {
  id: number;
  name: string;
};

const asynchFetchSim = async (): Promise<prop> => {
  await new Promise((resolve) => setTimeout(resolve, 2000));

  return {
    id: 123,
    name: "Hello Solar System!",
  };
};

export default async function FetchTest() {
  const data = await asynchFetchSim();

  return (
    <div>
      <h1>ID : {data.id}</h1>
      <p>NAME : {data.name}</p>
    </div>
  );
}
